@import '../../../MediaQueries/MediaQueries.scss';


@mixin center_flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.landing_container {
  height:100vh;
  width:100vw;
  background:#214054;
  @include center_flex();
  flex-direction: row;
  margin-bottom:50px;
}

.landing_lime {
  margin-top:30px;
}

.landing_page_heading {
  font-family: 'Oswald', sans-serif;
  text-transform: uppercase;
  color:#f4ffab;
  font-weight: 200;
  line-height: 1.2em;
  font-size: 4.7em;
  text-align: left;
  margin:0;
  margin-top:60px;
}

.landing_page_text {
  font-family: 'Oswald', sans-serif;
  color:#f4ffab;
  font-weight: 200;
  font-size: 1.5em;
  margin-top:5px;
  text-align: left;
}

a.name {
  text-decoration: none;
  color:#7ebfb6;
  font-weight: 400;
  font-family: 'Oswald', sans-serif;
}

a.name:hover {
  color: #f4ffab;
}

.landing_page_name {  
  text-align: left;
  margin-top:-30px;
}
 
img.profile_button {
  height:45px;
  width:45px;
  margin:12px;
}

.profile_buttons {
 margin-top:5px;
 margin-left:-12px;
}

.profile_button:hover {
  animation: scale1 2s ease infinite ;
}


// KEYFRAMES 
@keyframes name_scale {
  0% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(.9);
  }
  100% {
    transform: scale(1.1);
  }
}

@keyframes scale1 {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
} 

/* Star Animations */
@keyframes stars {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-2000px);
  }
}

// Media Query Landing Container
.landing_page {
  @media(max-width: $breakpoint-tablet){
    width:100vw;
  }

}

.landing_container {
  @media (max-width:$breakpoint-ipad-pro) {
    @include center_flex();
    flex-direction: column;
  }
}

// Heading MQ
.landing_page_heading {
  @media(max-width: $breakpoint-ipad-pro) {
    font-weight: 200;
    line-height: 1.2em;
    font-size:100px;
    text-align: center;
    margin:10px;
    margin-top:180px;
    }

    @media(max-width: $breakpoint-tablet) {
    font-weight: 200;
    line-height: 1.2em;
    font-size:70px;
    text-align: center;
    margin:10px;
    margin-top:250px;
    }
    @media( max-width: $breakpoint-iphone-plus){
      font-size:45px;
      margin-top:220px;
      margin-bottom:20px;
     }
    @media( max-width: $breakpoint-iphonex){
     font-size:45px;
     margin-top:150px;
     margin-bottom:20px;
    }
    @media( max-width: $breakpoint-iphone5){
      font-size:30px;
      margin-top:210px;
      margin-bottom:20px;
     }
  }

  // Buttons
  .profile_buttons {
    @media( max-width: $breakpoint-iphone-plus) {
      margin-left:0px;
    }
    @media( max-width: $breakpoint-iphonex) {margin-left:0px;
    }
  }
  img.profile_button {
    @media(max-width: $breakpoint-ipad-pro) and (min-width:$breakpoint-tablet +1px) {
      height:60px;
      width:60px;
      margin:15px;
    }
    @media(max-width: $breakpoint-tablet) {
      margin:10px;
    }
    @media( max-width: $breakpoint-iphone-plus){
      height: 33px;
      width:33px;
      margin:5px;
     }
    @media( max-width: $breakpoint-iphonex){
      height: 33px;
      width:33px;
      margin:5px;
     }
     @media( max-width: $breakpoint-iphone5){
      height: 26px;
      width:26px;
      margin:3px;
     }
  }

  .landing_page_text  {
    @media(max-width: $breakpoint-ipad-pro){
      text-align:center;
      font-size:28px;
    }
    @media(max-width: $breakpoint-tablet){
      font-size:22px;
    }
    @media(max-width: $breakpoint-iphone-plus){
      font-size:18px;
      // margin:30px;
    }
    @media(max-width: $breakpoint-iphonex){
      font-size:18px;
      // margin:30px;
    }
    @media(max-width: $breakpoint-iphone5){
      font-size:13px;
     
    }
  }

.landing_page_name {
  @media(max-width: $breakpoint-ipad-pro){
    text-align:center;
    font-size:25px;
  }

  @media(max-width: $breakpoint-tablet){
    font-size:18px;
  }
  @media(max-width: $breakpoint-iphone-plus){
    font-size:16px;
  }
  @media(max-width: $breakpoint-iphonex){
    font-size:16px;
    margin-top:-20px;
  }
  @media(max-width: $breakpoint-iphone5){
    font-size:10px;
    margin-top:-20px;
  }
  
}


// Landing Lime
.landing_lime {
  @media(max-width: $breakpoint-ipad-pro) {
    transform: scale(1.3);
    margin-top:-20px;
  }
  @media(max-width: $breakpoint-tablet) {
    transform: scale(.9);
    margin-top:-100px;
  }
  @media(max-width: $breakpoint-iphone-plus) {
    transform: scale(.7);
    margin-top:-160px;
  }
  @media(max-width: $breakpoint-iphonex) {
    transform: scale(.75);
    margin-top:-150px;
  }
  @media(max-width: $breakpoint-iphonex) {
    transform: scale(.5);
    margin-top:-185px;
  }
}

// Arrows
.arrows {
  @include center_flex();
  flex-direction:column;
  margin-top:-100px;
  margin-bottom:200px;
}

.arrow1 {
  @include center_flex();
  padding:10px;
}

.arrow1_left{
  height:10px;
  width:150px;
  background-color:#f4ffab;
  transform: rotate(20deg)translate(7px);
}

.arrow1_right {
  height:10px;
  width:150px;
  background-color:#f4ffab;
  transform: rotate(-20deg) translate(-7px);
}

.arrow2{
  @include center_flex();
  padding:15px;
}

.arrow2_left{
  height:6px;
  width:100px;
  background-color:#f4ffab;
  transform: rotate(20deg)translate(5px);
}

.arrow2_right {
  height:6px;
  width:100px;
  background-color:#f4ffab;
  transform: rotate(-20deg) translate(-5px);
}

